.noPage{
  padding: 200px 0;
  text-align: center;
  h1{
    letter-spacing: 10px;
    font-size: 100px;
  }
  p{
    font-size: 60px;
  }
}

@media screen and (max-width: 768px){
  .noPage{
    padding: 100px 0;
    h1{
      font-size: 60px;
    }
    p{
      font-size: 40px;
    }
  }
}
